
import 'antd/dist/reset.css';
import "./globals.css";
import { Poppins, Roboto } from 'next/font/google';
import Header from "./components/Header";
import Footer from "./components/Footer";

// Configure the Poppins font with all the weights and styles you need
const poppins = Poppins({
  weight: ['100', '200', '300', '400', '500', '600', '700', '800', '900'],
  subsets: ['latin'],
  display: 'swap',
  variable: '--font-poppins',
});

// Configure the Roboto font with all the weights and styles you need
const roboto = Roboto({
  weight: ['100', '300', '400', '500', '700', '900'],
  subsets: ['latin'],
  display: 'swap',
  variable: '--font-roboto',
});

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${poppins.variable} ${roboto.variable} font-sans antialiased`}>
        <style jsx global>{`
          :root {
            --font-poppins: ${poppins.style.fontFamily};
            --font-roboto: ${roboto.style.fontFamily};
          }
        `}</style>
        <Header/>
        {children}
        <Footer/>
      </body>
    </html>
  );
}
